import React, {Fragment, Component} from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import posed, {PoseGroup} from 'react-pose';
import {connect} from 'react-redux';
//components import
import {Navbar, Footer} from './layout';
import Home from './Home';
import Experiences from './Experiences';
import Services from './Services';
import NoPage from './404.js';
//actions import
import * as rootAct from '../store/actions/rootAct';

const RouteContainer = posed.div({
  enter: {opacity: 1, beforeChildren: true},
  exit: {opacity: 0},
});

class App extends Component {
  render() {
    const {rootData} = this.props;
    return (
      <Router>
        <Route
          render={({location}) => (
            <Fragment>
              <div id="root-content">
                <Navbar data={rootData} />
                <div id="box">
                  <PoseGroup>
                    <RouteContainer key={location.pathname}>
                      <Switch location={location}>
                        <Route
                          exact
                          path="/"
                          render={props => (
                            <Home
                              data={rootData}
                              {...props}
                              setN={this.props.setName}
                            />
                          )}
                          key="home"
                        />
                        <Route
                          path="/experiences"
                          component={Experiences}
                          key="exp"
                        />
                        <Route
                          path="/services"
                          component={Services}
                          key="services"
                        />
                        <Route component={NoPage} />
                      </Switch>
                    </RouteContainer>
                  </PoseGroup>
                </div>
              </div>
              <Footer />
            </Fragment>
          )}
        />
      </Router>
    );
  }
}

const mapState = state => {
  return {
    rootData: state.rootRed,
  };
};

const mapDispatch = dispatch => {
  return {
    setName: name => {
      dispatch(rootAct.setName(name));
    },
  };
};

export default connect(
  mapState,
  mapDispatch,
)(App);
