const data = {
  name: 'Arfani Hidayat',
  profesi: ['Computer Engineering', 'Informatics Engineering'],
};

const rootRed = (state = data, action) => {
  switch (action.type) {
    case 'set_name':
      state = {...data, name: action.name};
      break;
    default:
      break;
  }

  return state;
};

export default rootRed;
