import React, {Component} from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import {NavLink} from 'react-router-dom';
import Swal from '../CS';

export default class NavBar extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.collapse = this.collapse.bind(this);
    this.cs = this.cs.bind(this);
    this.state = {
      isOpen: false,
      swalShow: false,
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  }

  collapse() {
    this.setState({
      isOpen: false,
    });
  }

  cs() {
    this.setState({
      swalShow: !this.state.swalShow,
    });
  }

  componentDidMount() {
    window.addEventListener('scroll', this.changeNav);
  }

  changeNav() {
    const distanceY = window.pageYOffset || document.documentElement.scrollTop;
    const shrinkOn = 40;
    const nav = document.getElementById('navbar');

    distanceY > shrinkOn
      ? nav.classList.add('isScrollDown')
      : nav.classList.remove('isScrollDown');
  }

  render() {
    return (
      <Navbar className="fixed-top" color="light" light expand="md" id="navbar">
        <NavbarBrand id="nameBrand" href="/">
          {this.props.data.name}
        </NavbarBrand>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink
                exact
                to="/"
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}>
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to="/experiences"
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}>
                Experiences
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to="/services"
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}>
                Services
              </NavLink>
            </NavItem>
            <NavItem>
              <button
                className="nav-link"
                onClick={this.cs}
                style={{background: 'transparent'}}>
                Portfolio
              </button>
            </NavItem>

            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret title="Select your language">
                Lang
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem onClick={this.collapse}>English</DropdownItem>
                <DropdownItem onClick={this.cs}>Bahasa Indonesia</DropdownItem>
                <DropdownItem onClick={this.cs}>Sasak (Lombok)</DropdownItem>
                <DropdownItem onClick={this.cs}>العربية</DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
        <Swal
          show={this.state.swalShow}
          onConfirm={() => this.setState({swalShow: false})}
        />
      </Navbar>
    );
  }
}
